import LayersOutlinedIcon from "@mui/icons-material/LayersOutlined";
import PersonIcon from '@mui/icons-material/Person';
// import InsertCommentIcon from '@mui/icons-material/InsertComment';
// import Label from '../../components/Label';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
import SettingsIcon from '@mui/icons-material/Settings';
import SupportAgentIcon from '@mui/icons-material/SupportAgent';
// components
import SvgIconStyle from '../../components/SvgIconStyle';
import jwt_decode from 'jwt-decode'
// import { varBounceInDown } from "src/components/animate";

var access = localStorage.getItem('token') ? jwt_decode(localStorage.getItem('token')) : false

// ----------------------------------------------------------------------

const getIcon = (name) => (
  <SvgIconStyle src={`/static/icons/navbar/${name}.svg`} sx={{ width: '100%', height: '100%' }} />
);

const ICONS = {
  blog: getIcon('ic_blog'),
  cart: getIcon('ic_cart'),
  chat: getIcon('ic_chat'),
  mail: getIcon('ic_mail'),
  user: getIcon('ic_user'),
  kanban: getIcon('ic_kanban'),
  banking: getIcon('ic_banking'),
  calendar: getIcon('ic_calendar'),
  ecommerce: getIcon('ic_ecommerce'),
  analytics: getIcon('ic_analytics'),
  dashboard: getIcon('ic_dashboard'),
  booking: getIcon('ic_booking')
};

var sidebarConfig = []
if (access && access.role === 'admin') {
  sidebarConfig.push(
    {
      subheader: '',
      items: [
        {
          title: 'dashboard',
          path: PATH_DASHBOARD.general.index,
          icon: ICONS.analytics,
        },
        {
          title: 'mail',
          path: PATH_DASHBOARD.mail.root,
          icon: ICONS.mail,
          // info: <Label color="error">2</Label>
        },
        {
          title: 'users',
          path: '/dashboard/accounts',
          icon: <PersonIcon />,
        },
        {
          title: "Contacts",
          path: '/dashboard/contact',
          icon: <LayersOutlinedIcon />,
        },
        {
          title: "Email setting",
          path: '/dashboard/esetting',
          icon: <SettingsIcon />,
        },
        {
          title: "Service",
          path: '/dashboard/service',
          icon: <SupportAgentIcon />,
        },
      ]
    }
  )
}
else if (access && access.role === 'user') {
  sidebarConfig.push(
    {
      subheader: '',
      items: [
        {
          title: 'dashboard',
          path: PATH_DASHBOARD.general.index,
          icon: ICONS.analytics,
        },
      ]
    }
  )
}


export default sidebarConfig;
